import FlyLine from './FlyLIne/index.js'
import AirLoading from './AirLoading/index.js'
import RingChart from './RingChart/index.js'
import FlowChart from './FlowChart/index.js'
import DashChart from './DashChart/index.js'
import DashRatioChart from './DashRatioChart/index.js'
import FlashMarker from './FlashMarker/index.js'
import FlyLine2 from './FlyLine2/index.js'
import GradientDashChart from './GradientDashChart/index.js'
import BesselArcFlyline from './BesselArcFlyline/index.js'
// import PhotoWall from './PhotoWall/index.js'
// 
const components = [
  FlyLine,
  AirLoading,
  RingChart,
  FlowChart,
  DashChart,
  DashRatioChart,
  FlashMarker,
  FlyLine2,
  GradientDashChart,
  BesselArcFlyline
]

//  install  Vue  use 
const install = function (Vue) {
  // 
  components.forEach(component => Vue.component(component.name, component))
}

// 
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}


export {
  install,
  FlyLine,
  AirLoading,
  RingChart,
  FlowChart,
  DashChart,
  DashRatioChart,
  FlashMarker,
  FlyLine2,
  GradientDashChart,
  BesselArcFlyline
}

export default {
  //  install Vue.use() 
  install,
  FlyLine,
  AirLoading,
  RingChart,
  FlowChart,
  DashChart,
  DashRatioChart,
  FlashMarker,
  FlyLine2,
  GradientDashChart,
  BesselArcFlyline
}
